import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleUp } from '@fortawesome/free-solid-svg-icons';
import { Link } from 'react-router-dom';

function Header(props) {
  const [menuOpen, setMenuOpen] = useState(false);
  const [visibleUp, setVisibleUp] = useState(false);
  const scrollTop = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  useEffect(() => {
    const handleScroll = () => {
      setVisibleUp(window.scrollY > 200);
    };    
    window.addEventListener('scroll', handleScroll);
    return() => {
      window.removeEventListener('scroll', handleScroll);
    };
  });

  return (
    <header>
      <button onClick={()=>{setMenuOpen(true)}}><img src={`${process.env.PUBLIC_URL}/images/topIcon_burger.png`} alt='' /></button>
      <h1><img src={`${process.env.PUBLIC_URL}/images/logo_clr.png`} alt="" /></h1>
      <button><img src={`${process.env.PUBLIC_URL}/images/topIcon_cart.png`} alt='' /></button>

      <nav className='navi' style={{left:menuOpen?'0%':'-100%'}}>
      <button className='close_btn' onClick={()=>{setMenuOpen(false)}}>
        <img src={`${process.env.PUBLIC_URL}/images/btn_close.png`} alt="Close" />
      </button>

      <ul className='gnb'>
        <li><Link to='/ginipet' title=' ' onClick={()=>{setMenuOpen(false)}}> </Link></li>
        <li><Link to='/intro' title=' ' onClick={()=>{setMenuOpen(false)}}> </Link></li>
        <li><Link to='/info' title=' ' onClick={()=>{setMenuOpen(false)}}> </Link></li>
        <li><Link to='/event' title='' onClick={()=>{setMenuOpen(false)}}></Link></li>
        <li><Link to='/customer' title='' onClick={()=>{setMenuOpen(false)}}></Link></li>
      </ul>

      <ul className='form_navi'>
        <li><Link to='/login' title='' onClick={()=>{setMenuOpen(false)}}></Link></li>
        <li><Link to='/join' title='' onClick={()=>{setMenuOpen(false)}}></Link></li>
        <li><Link to='/order' title='' onClick={()=>{setMenuOpen(false)}}></Link></li>
        <li><Link to='/cart' title='' onClick={()=>{setMenuOpen(false)}}></Link></li>
      </ul>
    </nav>

    {visibleUp && (
      <button className='top_btn' onClick={scrollTop}><FontAwesomeIcon icon={faAngleUp} /></button>
    )}
    </header>
  );
}

export default Header;